.image-stack {
  display: grid;
  position: relative;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(10, 60px);
  rotate: z 1deg;
}

.image-stack__item--bottom {
  grid-column: 1 / span 8;
  grid-row: 1;
  z-index: 1;
  /* position: relative; */
}

.image-stack__item--top2 {
  grid-row: 1;
  grid-column: 1 / span 8;
  z-index: 4;

  /* position: relative; */
}


.image-stack__item--top {
  grid-row: 1;
  grid-column: 1 / span 8;
  z-index: 3;

  /* position: relative; */
}

.image-stack__item--middle {
  grid-row: 1;
  grid-column: 1 / span 8;
  z-index: 2;

  /* position: relative; */
}

.image-stack__item--top-trick-title {
  grid-row: 5;
  grid-column: 4;
  z-index: 5;
  margin-top: 7%;
}

img {
  width: 100%;
  display: block;
  position: relative;
}

